import { HANDSOME,NOHANDSOME } from "../constants";

import axios from 'axios'
export function handSome(add) {
  /*
    这里的action返回的是一个函数而不是对象，因为引入了redux-thunk这个中间件
   */
  return dispacth =>{
    setTimeout(()=>{
      dispacth({
        type:HANDSOME,
        add
      })
    },3000)
  }
  // return{
  //   type:HANDSOME,
  //   add
  // }
}

export function noHandSome(add) {

  return{
    type:NOHANDSOME,
    payload:axios.get('https://randomuser.me/api/')
  }
}





