import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Header from './App';
import registerServiceWorker from './registerServiceWorker';
import { Provider } from  'react-redux'
import reducer from './reducers'
import logger from 'redux-logger'
import {createStore, applyMiddleware} from 'redux'
import {composeWithDevTools} from 'redux-devtools-extension'
import thunk from 'redux-thunk'//该中间件允许actions中返回一个函数而不是对象，这就允许我们在actions添加异步操作了
import reduxPromise from 'redux-promise-middleware'
//dispatchAction ----> 多个middleware ----> reducers ----> store


import configStore from './store/configStore'
console.log(configStore)



const demo = store => next => action => {
  console.log(store.getState())
  console.log(action)
  console.log(next(action) == action)
}
const store  = createStore(reducer,{},composeWithDevTools(applyMiddleware(logger,thunk,reduxPromise())))

// store.dispatch({
//   type:'handSome'
// })
ReactDOM.render(
  <Provider store={store}>
    <Header />
  </Provider>
  , document.getElementById('root')
);
registerServiceWorker();



