class Header extends Component{
  constructor(){
    super()
      this.state ={
        state1:'1'
      }
      /*
        constructor中是唯一能够初始化state的地方
       */
  }
    get(){
    console.log(111)
      axios.post('http://172.16.110.110:414/quota/quotaList')
    }

    sonToFather(valFromSon){
      console.log(valFromSon)
    }
    render(){
    // const { dispatch } = this.props 第一种dispatch
      const { handSome, noHandSome } = this.props
        return(
          /*
            在render函数中使用props时要在前面加上this
          */
          <div>
            <Son name={[1,2,3,4]} sonToFather={this.sonToFather}>
              <p>hahahha</p>
              {/*类似slot*/}
            </Son>
            <button onClick={()=>handSome(123)}>dispatch</button>
            <button onClick={()=>noHandSome(456)}>dispatch</button>
            <p>
              {this.props.value}
            </p>
            <button onClick={this.get }>axios</button>
          </div>



        );
    }
}

const mapStateToProps = (state) =>{
  return {
    value:state.add
  }
}
