const mapDispatchToProps = (dispatch) =>{
  /*
      一般用于父组件传递给子组件，所以一般不用这种
   */
   return bindActionCreators({handSome,noHandSome},dispatch)
}

//第五种dispatch

// export default connect(mapStateToProps,{handSome:handSome,noHandSome:noHandSome})(Header)
export default connect(mapStateToProps,mapDispatchToProps)(Header);



